import FormFieldWrap from "@/components/FormFieldWrap";

export default {
  components: {
    FormFieldWrap,
  },
  props: {
    type: {
      type: String,
      default: "text",
    },
    title: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
    },
    error: {
      type: String,
    },
    value: {
      required: true,
    },
  },
  computed: {
    inputValue: {
      get() {
        return this.value;
      },
      set(newValue) {
        this.$emit("input", newValue);
      },
    },
  },
};
