<template>
  <div>
    <BaseHeader />
    <main class="content container">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <RouterLink class="breadcrumbs__link" :to="{ name: 'home' }">
              Каталог
            </RouterLink>
          </li>
          <li class="breadcrumbs__item">
            import BasePreloader from "@/components/BasePreloader.vue";
            <RouterLink class="breadcrumbs__link" :to="{ name: 'home' }">
              Корзина
            </RouterLink>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link"> Оформление заказа </a>
          </li>
        </ul>

        <div class="content__row">
          <h1 class="content__title">Оформление заказа</h1>
        </div>
      </div>

      <section class="cart">
        <form
          class="cart__form form"
          action="#"
          method="POST"
          @submit.prevent="submitFormHandler"
        >
          <div class="cart__field">
            <div class="cart__data">
              <FormFieldInput
                title="ФИО"
                placeholder="Введите ваше полное имя"
                v-model="formData.fio"
                :error="formError.fio"
              />
              <FormFieldInput
                title="Адрес доставки"
                placeholder="Введите ваш адрес"
                v-model="formData.addr"
                :error="formError.addr"
              />
              <FormFieldInput
                title="Телефон"
                type="tel"
                placeholder="Введите ваш телефон"
                v-model="formData.tel"
                :error="formError.tel"
              />
              <FormFieldInput
                title="Email"
                type="email"
                placeholder="Введи ваш Email"
                v-model="formData.email"
                :error="formError.email"
              />
              <FormFieldTextarea
                title="Комментарий к заказу"
                placeholder="Ваши пожелания"
                v-model="formData.comment"
                :error="formError.comment"
              />
            </div>

            <div class="cart__options">
              <h3 class="cart__title">Доставка</h3>
              <div class="with-item-preloader">
                <ul class="cart__options options" v-if="deliveries.length">
                  <li
                    class="options__item"
                    v-for="delivery in deliveries"
                    :key="delivery.title"
                  >
                    <label class="options__label">
                      <input
                        class="options__radio sr-only"
                        type="radio"
                        name="delivery"
                        :value="delivery.id"
                        v-model="currentDelivery"
                      />
                      <span class="options__value">
                        {{ delivery.title }}
                        <b v-if="delivery.price != 0"
                          >{{ delivery.price | numberFormat }} ₽</b
                        >
                        <b v-else>бесплатно</b>
                      </span>
                    </label>
                  </li>
                </ul>
                <BasePreloader v-if="!deliveries.length" is-item-preloader />
              </div>

              <h3 class="cart__title" v-if="currentDelivery">Оплата</h3>
              <div class="with-item-preloader" v-if="currentDelivery">
                <ul
                  class="cart__options options"
                  v-if="payments.length && !paymentsLoading"
                >
                  <li
                    class="options__item"
                    v-for="payment in payments"
                    :key="payment.title"
                  >
                    <label class="options__label">
                      <input
                        class="options__radio sr-only"
                        type="radio"
                        name="payment"
                        :value="payment.id"
                        v-model="currentPayment"
                      />
                      <span class="options__value"> {{ payment.title }} </span>
                    </label>
                  </li>
                </ul>
                <BasePreloader
                  v-if="currentDelivery && paymentsLoading"
                  is-item-preloader
                />
              </div>
            </div>
          </div>

          <div class="cart__block">
            <ul class="cart__orders">
              <li
                class="cart__order"
                v-for="product in productsForCartPage"
                :key="product.itemId"
              >
                <h3>
                  {{ product.title }}
                  <span v-if="product.amount > 1">({{ product.amount }})</span>
                </h3>
                <b>{{ (product.price * product.amount) | numberFormat }} ₽</b>
                <div class="cart__order-info">
                  <i class="cart__order-color"
                    >Цвет:<span
                      :style="{ backgroundColor: product.color.code }"
                    ></span
                  ></i>

                  <i>Размер: {{ product.size.title }}</i>
                </div>
                <span>Артикул: {{ product.id }}</span>
              </li>
            </ul>

            <div class="cart__total">
              <p v-if="curDelivery.title">
                {{ curDelivery.title }}:
                <b v-if="curDeliveryPrice">{{
                  curDeliveryPrice | numberFormat
                }}</b>
                <b v-else>бесплатно</b>
              </p>
              <p>
                Итого: <b>{{ productsLength }}</b>
                {{ "товар" | normalizeWord(productsLength) }} на сумму
                <b>{{ (totalAmount + curDeliveryPrice) | numberFormat }} ₽</b>
              </p>
            </div>

            <button class="cart__button button button--primery" type="submit">
              Оформить заказ
            </button>
          </div>
          <div class="cart__error form__error-block">
            <h4>Заявка не отправлена!</h4>
            <p>
              Похоже произошла ошибка. Попробуйте отправить снова или
              перезагрузите страницу.
            </p>
          </div>
        </form>
      </section>
    </main>
    <BaseFooter />
  </div>
</template>

<script>
import BaseFooter from "@/components/BaseFooter.vue";
import BaseHeader from "@/components/BaseHeader.vue";
import BasePreloader from "@/components/BasePreloader.vue";
import FormFieldInput from "@/components/FormFieldInput.vue";
import FormFieldTextarea from "@/components/FormFieldTextarea.vue";
import numberFormat from "@/helpers/number-format";
import normalizeWord from "@/helpers/normalise-word";
import { RouterLink } from "vue-router";
import { mapGetters } from "vuex";

export default {
  name: "OrderPage",
  components: {
    BaseHeader,
    BaseFooter,
    BasePreloader,
    RouterLink,
    FormFieldInput,
    FormFieldTextarea,
  },
  data() {
    return {
      formData: {},
      formError: {},
      currentDelivery: 0,
      currentPayment: 0,
    };
  },
  watch: {
    currentDelivery(value) {
      if (value > 0) {
        this.$store.dispatch("getPaymentsByDelivery", this.currentDelivery);
      }
    },
  },
  methods: {
    submitFormHandler() {
      console.log("subm");
    },
  },
  computed: {
    ...mapGetters([
      "productsLength",
      "totalAmount",
      "cartLoading",
      "productsForCartPage",
      "cartBlocked",
      "deliveriesLoading",
      "deliveriesBlocked",
      "deliveries",
      "paymentsLoading",
      "paymentsBlocked",
      "payments",
    ]),
    curDelivery() {
      return (
        this.deliveries.find((it) => it.id === this.currentDelivery) || {
          price: 0,
        }
      );
    },
    curDeliveryPrice() {
      return Number(this.curDelivery.price);
    },
    curDeliveryTitle() {
      return this.curDelivery.title;
    },
  },
  filters: {
    numberFormat,
    normalizeWord,
  },
  created() {
    this.$store.dispatch("getDeliveries");
  },
};
</script>

<style lang="scss" scoped>
.cart__order-info {
  grid-column: 1 / -1;
  font-size: 0.8em;

  & *:not(:last-child) {
    margin-right: 10px;
  }
}

.cart__order-color {
  position: relative;
  padding-right: 1.1em;

  span {
    position: absolute;
    top: 50%;
    right: 0;
    border-radius: 50%;
    display: block;
    width: 0.7em;
    height: 0.7em;
    transform: translateY(-50%);
  }
}
</style>
